import React, { Component } from 'react'
import logo from './logo.svg'
import styled from 'styled-components'
import content from "./content.json"
import Map from "./Components/Map"
import About from "./Components/About"
import Calendar from "./Components/Calendar"
import './App.css'

import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch
} from 'react-router-dom'

const routes = { home: "/", about: "/about", calendar: "/calendar" }

class App extends Component {
  render() {
    return (
      <div>
        <Header />
        <Main />
      </div>
    )
  }
}

class Main extends Component {
  render() {
    return (
    <main>
      <Switch>
        <Route exact path={routes["home"]} component={Map}/>
        <Route path={routes["about"]} component={About}/>
        <Route path={routes["calendar"]} component={Calendar}/>
      </Switch>
    </main>
    )
  }
}

class Header extends Component {
  constructor(props) {
    super(props)

    this.state = { selected: "home", showTitle: true }
  }

  render() {
    const adjustedFontSize = 30
    const linkStyles = {fontSize: adjustedFontSize}
    const listItemStyle = { fontFamily: 'Klima Bold', fontSize: adjustedFontSize }
    const titleStyle = { fontFamily: 'Klima Bold', fontSize: 20, marginLeft: "10px" }
    
    const alignLeft = { marginLeft: -20 }
  
    return (
      <navbar>
			<nav>
				<div className="navWide">
					<div className="wideDiv">
          {this.listItems()}
        </div>
				</div>
				<div className="navNarrow">
          <i className="fa fa-bars fa-2x" onClick={this.burgerToggle}></i>
          <div style={titleStyle}>{ this.state.showTitle && this.titleFor(this.state.selected) }</div>
          <div className="narrowLinks">
            { this.listItems(true) }
					</div>
				</div>
      </nav>
      </navbar>
		)
  }

  burgerToggle(selected) {
    let linksEl = document.querySelector('.narrowLinks');
    if (selected && this) { this.setState({ selected }) }
    
    if (!linksEl) return
    if (linksEl.style.display === 'block') {
			linksEl.style.display = 'none';
		} else {
			linksEl.style.display = 'block';
		}
  }
  
  listItems(narrow) {
    const adjustedFontSize = narrow ? 22 : 30    
    const linkStyles = {fontSize: adjustedFontSize, marginLeft: "5px", marginRight: "10px"}
    const listItemStyle = { fontFamily: 'Klima Bold', fontSize: adjustedFontSize, color: "black"}
    const separator = narrow || "/"

    return (
      <div>
        <li style={listItemStyle} ><Link style={linkStyles} to={routes["home"]} onClick={this.burgerToggle.bind(this, "home")}>{ this.titleFor("home", narrow)}</Link>{separator}</li>
        <li style={listItemStyle} ><Link style={linkStyles} to={routes["about"]} onClick={this.burgerToggle.bind(this, "about")}>{this.titleFor("about", narrow)}</Link>{separator}</li>
        <li style={listItemStyle} ><Link style={linkStyles} to={routes["calendar"]} onClick={this.burgerToggle.bind(this, "calendar")}>{this.titleFor("calendar", narrow)}</Link></li>
      </div>
    )
  }

  titleFor(what, narrow) {
    switch(what) {
      case "home": {
        return narrow ? "home" : "From a Sea of Islands to the Rhineland(s)"
      }
      case "about": {
        return narrow ? "about" : "About"
      }
      case "calendar": {
        return narrow ? "cop23 calendar" : "COP23 Calendar"
      }
    }
  }
}

export default App






