import React, { Component } from 'react'
import styled from 'styled-components'


const Description = styled.div`
width: 700px;
`

class Story extends Component {
  
    title() {
      const story = this.props.selectedStory
      const style = { fontFamily: "Klima Bold" }
      if (story.link) {
        return (
          <a href={story.link} style={style}>{story.title}</a>
        )
      }
      return (
        <h1 style={style}>{story.title}</h1>
      )
    }
  
    render() {
      const story = this.props.selectedStory
      const video = story && story.video
      if (!story) return null
  
      const subtitleStyle = {
        fontSize: 16,
        fontStyle: "italic",
        fontFamily: "Klima"
      }
  
      return (
        <div style={{marginLeft: 20}}>
          { this.title() }
          { story.author && <h1 style={subtitleStyle}>{story.author + ' • ' + story.place}</h1> }
          { story.description && <Description>{story.description}</Description> }
          { video ? <YouTube video={video} autoplay="0" rel="0" modest="1" /> : <div style={{height: 500, width: 10}}/> }
        </div>
      )
    }
  }
  
  class YouTube extends Component {
    render() {
        const fbVideo = this.props.video.includes("facebook")
        var videoSrc

        if (fbVideo) {
          videoSrc = this.props.video
        } else {
          videoSrc = "https://www.youtube.com/embed/" + 
          this.props.video + "?autoplay=" + 
          this.props.autoplay + "&rel=" + 
          this.props.rel + "&modestbranding=" +
          this.props.modest;
        }
        return (
          <div style={{ width: 640, height: 390, marginTop: "20px", marginBottom: "40px"}}>
            <div className="holds-the-iframe" style={{width: 600, height: 350}}>
              <iframe className="player" type="text/html" width="640" height="390" src={videoSrc} frameBorder="0"/>
            </div>
          </div>
        )
      }
  }

  export default Story


